.blazen {
  background-color: #333;
  position: sticky;
  img {
    border: 0px solid rgba(0,0,0,.5);
    box-shadow: 0px 0px 50px rgba(204,204,204,0.3);
    border-radius: 100px;
    background-color: #3f3028;
  }
}

.parallax {
  background-image: url("/assets/images/house/SOAL3.jpg");

  height: 100vh;
  width: 100vw;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  position: relative;
  opacity: 0.65;
}
